import React from 'react'
import Link from 'gatsby-link'
import img1 from '../image/Artboard.jpg'
import img2 from '../image/Artboard2.jpg'

const AdminOrder = () => (
  <div>
    <img src={img1} alt="admin_order_image1"/>
    <img src={img2} alt="admin_order_image2"/>
  </div>
)

export default AdminOrder



